@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "TmonMonsoriRegular";
  src: local("TmonMonsoriRegular"),
    url("./assets/Tmon-Monsori-Regular.ttf") format("truetype");
  font-weight: normal;
}

@font-face {
  font-family: "HS봄바람체";
  src: local("HS봄바람체"),
    url("./assets/HS봄바람체2.0.ttf") format("truetype");
  font-weight: normal;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
